.image_url {
  border-top: 1px solid #35A7FF;
}

.image_url img{
  height: 300px;
  width: 85px;
  margin-top: 2rem;
}

.tagline,
.food-pairing,
.brewers-tips {
  color: #FFFFFF;
}

/* beer details */

.food-pairing-list li{
  list-style-type: none;
}

/* header */




/* searchbar */
.searchbar {
  margin-bottom: 2rem;
}


.searchbar input {
  border-radius: 5px;
  width: 20rem;
  padding: .50rem .75rem;
  font-family: 'Space Mono', sans-serif;
  font-size: .75rem;
}

.searchbar-submit {
  margin-bottom: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.searchbar-submit a {
  border: 1px solid #FFF;
  padding: 0.525rem 0.825rem;
  border-radius: 5px;
  background: #fff;
  color: #35A7FF;
  text-decoration: none;
  font-weight: 500;
}

.searchbar-submit a:hover{
  text-decoration: none;
  transition: background-color .5s;
  cursor: pointer;
  color: #fff;
  background-color: #35A7FF;
  border: 1px solid #35A7FF;
  text-align: center;
}

